import React from 'react'
import { css } from '@emotion/core'
import { Link } from 'Config/routes'
import { withRouter } from "next/router"
import PropTypes from 'prop-types'
import {
  Menu, Icon,
} from 'antd'

const { SubMenu } = Menu

const SideBar = ({ router }) => {
  const { pathname } = router
  return(
    <Menu
      selectedKeys={[pathname]}
      defaultOpenKeys={['sub1']}
      mode="inline"
      theme='light'
      css={css`
        border-right: 0;
        margin-top: 80px;
        li.ant-menu-item-selected {
          background-color: #ffe6e6;
          a {
            color: #ff1818;
          }
        }
      `}
    >
      <Menu.Item key="/dashboard">
        <Link route="dashboard">
          <a>
            <Icon type="home" />
            <span>Dashboard</span>
          </a>
        </Link>
      </Menu.Item>
      <SubMenu key="sub1" title={<span><Icon type="money-collect" />Pengaturan Tarif</span>}>
        <Menu.Item key="/priceSetting/price"><Link route="price">Tarif</Link></Menu.Item>
        <Menu.Item key="/priceSetting/discount"><Link route="discount">Diskon</Link></Menu.Item>
      </SubMenu>
      <Menu.Item key="/moneyReport">
        <Link route="moneyReport">
          <a>
            <Icon type="solution" />
            <span>Laporan Keuangan</span>
          </a>
        </Link>
      </Menu.Item>
      <Menu.Item key="/assetManagement">
        <Icon type="book" />
        <Link route="assetManagement">
          <span>Manajement Aset</span>
        </Link>
      </Menu.Item>
      <Menu.Item key="4">
        <Icon type="usergroup-add" />
        <Link route="">
          <span>Pengaturan User</span>
        </Link>
      </Menu.Item>
    </Menu>
  )
}

SideBar.propTypes = {
  router: PropTypes.string
}

export default withRouter(SideBar)